import { combineReducers } from "redux";

const counterReducer = (counter = 0, action) => {
  if (action.type == "INCREMENT") {
    return counter + 1;
  } else if (action.type == "DECREMENT") {
    return counter - 1;
  } else return counter;
};

export default combineReducers({
  counter: counterReducer,
});
